export default () => ({

    state: {
        incoming: []
    },

    mutations: {
        'bills/store': (state, incoming) => {
            state.incoming = incoming
        }
    },

    actions: {

        'bills/load': ({ commit, state }) => {
            return new Promise((resolve) => {
                setTimeout(() => {
                    commit('bills/store', [
                        { id: 1, purpose: '  . 1: ', value: 2000, status: null },
                        { id: 2, purpose: '  . 1:  ', value: 750, status: null },
                        { id: 3, purpose: '  . 1:   ', value: 1500, status: null },
                        { id: 4, purpose: '     : o001oo177', value: 500, status: null },
                        { id: 5, purpose: '  -: o001oo177', value: 500, status: null },
                        { id: 6, purpose: '    ', value: 12000, status: null }
                    ])
                    resolve(state.incoming)
                }, 200)
            })
        },

        'bills/accept': ({ commit, state }, { id }) => {
            return new Promise((resolve, reject) => {
                setTimeout(() => {
                    for (let b of state.incoming) {
                        if (b.id === id) {
                            b.status = 'accepted'
                            resolve(b)
                            return
                        }
                    }

                    reject({
                        message: 'No data'
                    })
                }, 200)
            })
        },

        'bills/decline': ({ commit, state }, { id }) => {
            return new Promise((resolve, reject) => {
                setTimeout(() => {
                    for (let b of state.incoming) {
                        if (b.id === id) {
                            b.status = 'declined'
                            resolve(b)
                            return
                        }
                    }

                    reject({
                        message: 'No data'
                    })
                }, 200)
            })
        }
    }
})



